export default {
	data() {
		return {
			flag:false,
			mapInfo: {}
		};
	},
	onLoad(res) {
		let that=this;
		console.log(JSON.parse(res.mapInfo));
		that.mapInfo = JSON.parse(res.mapInfo);
		setTimeout(function() {
			that.flag=true;
		}, 300);
	},
	methods: {}
};
